@import "../../styles/constants.scss";

body {
  background: #000;
  background: $backgroundGradient;

  .pageFrame {
    position: relative;
    min-height: 100vh;
    width: 100vw;
    padding-top: $headerHeight * 2;
    padding-bottom: $footerHeight;
    text-align: center;
    font-family: $fontFamily;
    color: white;
  }

  .content {
    margin-top: $base * 3;
    margin-bottom: $pageBottomMargin;
  }
}
